import React from 'react';
import { render } from 'react-dom';
import { csvParse } from "d3-dsv";
import { timeParse } from "d3-time-format";

function parseData(parse) {
	return function (d) {
		d.open = +d.Open;
		d.high = +d.High;
		d.close = +d.Close;
		d.low = +d.Low;
		d.volume = +d.Volume;
		d.date = parse(d.Date);
		return d;
	};
}

function getData(stockhistoryData, chartSelect) {
	if (chartSelect != "CandleStickChartForContinuousIntraDay") {
		var parseDateFormat = '%d-%m-%Y';
	} else {
		var parseDateFormat = '%d-%m-%Y %H:%M:%S';
	}

	const promiseMSFT = fetch(stockhistoryData)
		.then(response => response.text())
		.then(data => csvParse(data, parseData(timeParse(parseDateFormat))))
	return promiseMSFT;
}

export default class UDFinancialChart extends React.Component {

	componentDidMount() {
		const stockhistoryData = this.props.stockData;
		const chartSelect = this.props.chartType;
		const chartScript = this.props.importChart;

		Chart = null;
		switch (chartSelect) {
			case "MovingAverageCrossOverAlgorithmV2":
				Chart = React.lazy(() => import('./Chart-MovingAverageCrossOverAlgorithmV2'));
				break;

			case "CandleStickChartWithFullStochasticsIndicator":
				Chart = React.lazy(() => import('./Chart-CandleStickChartWithFullStochasticsIndicator'));
				break;

			case "CandleStickChartForContinuousIntraDay":
				Chart = React.lazy(() => import('./Chart-CandleStickChartForContinuousIntraDay'));
				break;

			case "CandleStickChartWithMACDIndicator":
				Chart = React.lazy(() => import('./Chart-CandleStickChartWithMACDIndicator'));
				break;

			case "CandleStickChartWithRSIIndicator":
				Chart = React.lazy(() => import('./Chart-CandleStickChartWithRSIIndicator'));
				break;

			case "CandleStickChartWithBollingerBandOverlay":
				Chart = React.lazy(() => import('./Chart-CandleStickChartWithBollingerBandOverlay'));
				break;

			case "CandleStickChartWithMA":
				Chart = React.lazy(() => import('./Chart-CandleStickChartWithMA'));
				break;

			default:
				Chart = React.lazy(() => import('./Chart-MovingAverageCrossOverAlgorithmV2'));
		}

		getData(stockhistoryData, chartSelect).then(data => {
			this.setState({ data })
		})

	}
	render() {
		if (this.state == null) {
			return <div>..loading chart data...</div>
		}

		return (
			// <UDFinancialChart />,
			<Chart data={this.state.data} />
		)
	}
}
